import './Post.module.css';
import logo from './img/basketball.png';

const Post = ({message, likeCount}) => {
  return (
    <div className="post">
      <img src={logo} alt="ava" />
      <p className="postText">{message}</p>
      <button>Like: {likeCount || 0}</button>
    </div>
  );
};

export default Post;