import './UserStatus.css';
import React, { useEffect, useState } from 'react';

const UserStatusWithHooks = ({status, updateUserStatus}) => {
  const [editMode, setEditMode] = useState(false);
  const [statusText, setStatusText] = useState(status);

  useEffect(() => {
    setStatusText(status)
  }, [status]) 

  const onChangeStatus = (e) => {
    setStatusText(e.target.value);
  }

  const toggleEditMode = () => {
    setEditMode(!editMode) 
  }

  const saveChanges = () => {
    updateUserStatus(statusText);
    toggleEditMode()
  }

  return (
    <div>
      {
        editMode ? 
          <div>
          <input onChange={onChangeStatus} value={statusText} type="text" autoFocus/>
          <button onClick={saveChanges}>Save</button>
          </div>
        :
          <div>
            <span onDoubleClick={toggleEditMode}>Status: {statusText}</span>
          </div>
      }
      
    </div>
  )
};


export default UserStatusWithHooks;