import React from "react";
import Profile from "./Profile";
import { connect } from "react-redux";
import { getProfile, getUserStatus, updateUserStatus } from "../../redux/profile-reducer";
import { withRouter } from "../common/witRouter";
import { withAuthRedirectHOC } from "../../hoc/withAuthRedirect";
import { compose } from "redux";
import { getStatusText, getUserId, getUserInfo } from "../../redux/selectors/profile-selectors";

class ProfileContainer extends React.Component  {
  componentDidMount() {
    const {router, userId, getProfile, getUserStatus} = this.props;
    const profileId = router.params.userId ?? userId;
    getProfile(profileId);
    getUserStatus(profileId)
  }


  render() {
    return <Profile {...this.props} />;
  }
};

const mapStateToProps = (state) => {
  return {
    userInfo: getUserInfo(state),
    statusText: getStatusText(state),
    userId: getUserId(state)
  }
}


export default compose(
  connect(mapStateToProps, { getProfile: getProfile, getUserStatus: getUserStatus, updateUserStatus }),
  withRouter, 
  withAuthRedirectHOC,
)(ProfileContainer);