import classes from './User-info.module.css';
import Preloader from '../../common/Preloader/Preloader'
// import UserStatus from './UserStatus/UserStatus';
import UserStatusWithHooks from './UserStatus/UserStatusWithHooks';

const UserInfo = ({userInfo, statusText, updateUserStatus}) => {
  if(!userInfo) {
    return <Preloader />
  }

  return (
    <div className={classes.userInfo}>
      <img src={userInfo.photos.large} height="200px" alt="avatar" className={classes.userAvatar} />
      <div className={classes.userData}>
        <div className={classes.userName}>{userInfo.fullName}</div>
        <div className={classes.userDataItem + ' userDataItem'}>
          About me:
          <span>{userInfo.aboutMe}</span>
        </div>
        <div className={classes.userDataItem}>
          Положение:
          <span>{userInfo.lookingForAJobDescription}</span>
        </div>
        <div className={classes.userDataItem}>
          Facebook:
          <span>{userInfo.contacts.facebook}</span>
        </div>
        <div className={classes.userDataItem}>
          VK:
          <span>{userInfo.contacts.vk}</span>
        </div>
        <div className={classes.userDataItem}>
          Twitter:
          <span>{userInfo.contacts.twitter}</span>
        </div>
        <div className={classes.userDataItem}>
          Instagram:
          <span>{userInfo.contacts.instagram}</span>
        </div>
        <div className={classes.userDataItem}>
          Web site:
          <span>{userInfo.contacts.website}</span>
        </div>

        <UserStatusWithHooks status={statusText} updateUserStatus={updateUserStatus}/>
      </div>

    </div>
  );
};

export default UserInfo;