const MyPosts = React.memo(({addNewPost, postData}) => {
  const onAddPost = (values) => {
    addNewPost(values.post);
  }

  return (
    <div className={classes.posts}>
       <h2>My posts</h2>

       <PostForm onSubmit={onAddPost} />
      
       <div className={classes.postsWrapper}>
         {postData.map((p, index) => <Post message={p.message} likeCount={p.likeCount} key={index} />)}
       </div>
    </div>
  )
})

export default MyPosts;